import React, { useEffect, useRef, useState } from 'react';
import styled from '@emotion/styled';

import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';

import Box from './Box'
import Chevron from './Chevron'
import useResponsive from '../contexts/mediaQuery/useResponsive';

const StyleBox = styled(Box)`
  img,
  picture {
    display: block;
  }
  .slick-track {
    display: flex;
    align-items: stretch;
  }
  .slick-arrow {
    height: auto;
    font-size: 0.75em;
    z-index: 1;
    color: black;
    &::before {
      content: none;
    }
  }
  .slick-prev {
    left: ${props => props.isMobile ? (props.charts ? 0 : '-2.75em') : '-4.5em'};
    top: ${props => props.isMobile && props.charts && '1em'};
    background: linear-gradient(90deg, rgba(192,192,192,0.3) 0%, #C0C0C0 50%);
    cursor: ${props => !props.activeItemIndex && 'not-allowed'};
    opacity: ${props => !props.activeItemIndex && 0.5};
    width: ${props => props.arrowWidth};
    :hover {
      background: linear-gradient(90deg, rgba(192,192,192,0.3) 0%, #C0C0C0 50%);
      color: black;
    }
  }
  .slick-next {
    right: ${props => props.isMobile ? (props.charts ? 0 : '-2.75em') : '-4.5em'};
    top: ${props => props.isMobile && props.charts && '1em'};
    background: linear-gradient(90deg, #C0C0C0 50%, rgba(192,192,192,0.3) 100%);
    opacity: ${props => (props.activeItemIndex == props.length - 1) && 0.5};
    cursor: ${props => (props.activeItemIndex == props.length - 1) && 'not-allowed'};
    width: ${props => props.arrowWidth};
    :hover {
      background: linear-gradient(90deg, #C0C0C0 50%, rgba(192,192,192,0.3) 100%);
      color: black;
    }
  }
`

const Carousel = ({
    children,
    width,
    flex,
    autoplay,
    arrows,
    slidesToShow,
    silderProps,
    typeIndex,
    setType,
    charts,
    ...props
  }) => {
  const [activeItemIndex, setActiveIndex] = useState(0)
  const slickRef = useRef()
  useEffect(() => {
    if (slickRef.current) {
      slickRef.current.slickGoTo(typeIndex)
    }
  }, [slickRef, typeIndex])
  const { isMobile } = useResponsive()
  const arrowWidth = isMobile ? (charts ? '1.5rem' : '2.25rem') : '2.5rem'
  const length = children.length
  return (
    <StyleBox
      position="relative"
      charts={charts}
      isMobile={isMobile}
      width={width}
      flex={flex}
      arrowWidth={arrowWidth}
      activeItemIndex={activeItemIndex}
      length={length}
      {...props}
    >
      <Slider
        autoplay={autoplay}
        prevArrow={<Chevron.Left isMobile={isMobile} width={arrowWidth} />}
        nextArrow={<Chevron.Right isMobile={isMobile} width={arrowWidth}  />}
        arrows={arrows}
        dots={false}
        ref={slickRef}
        autoplaySpeed={3500}
        speed={750}
        afterChange={(current) => setActiveIndex(current)}
        beforeChange={(from, to) => {
          if (typeof setType === 'function') {
            setType(to)
          }
        }}
        slidesToShow={slidesToShow || 1}
        {...silderProps}
      >
        {children}
      </Slider>
    </StyleBox>
  );
}

Carousel.defaultProps = {
  arrows: true,
  silderProps: {
    infinite: false,
  }
}

export default Carousel;
